.calendar {
    .weeks,.days {
        display: flex;
        align-items: center;
        text-align: center;
        div {
            padding: 5px;
            width: calc(100%/7);
            border: 1px solid #FFFFFF;
            cursor: default;
        }
    }
    .weeks {
        div {
            border: 1px solid #000;
            border-bottom: 0;
            border-top: 0;
            background-color: #000;
            color: #FFFFFF
        }
        div:first-child {
            border-left: none
        }
        div:last-child {
            border-left: none
        }
    }
    .days {
        div:hover {
            background-color: #000;
            color: #FFFFFF;
            cursor: pointer;
        }
    }
}